<template>
  <div v-if="view==1">
    <Event/>
    <div class="app_wrap">
      <NavBar></NavBar>
      <div class='shopHeaderWarp'>
        <div class="shopHeader">
          <div class="header_l">
            <a href="https://mvtk.jp?from=dkeitai">
              <img src="https://service.smt.docomo.ne.jp/keitai_payment/shop/images/movieticket/logo.jpg"/>
            </a>
          </div>
          <div class="header_r">
              <span>チケット予約・交通</span>
              <h1><a>ムビチケ</a></h1>
              <p>全国の映画館で利用できる、ネットで座席指定可能なデジタル映画鑑賞券。料金がオトクな「ムビチケ前売券」と、ポイントを使って購入できる「ムビチケ当日券」があります。</p>
          </div>
        </div>
        <div class="site_btn">
          <a href="https://mvtk.jp?from=dkeitai" target="_blank">サイトはこちら</a>
          <p>ムビチケは、一部端末で接続できない場合があります。</p>
        </div>
      </div>
      <div class="shopContents">
        <h2>キャンペーン情報</h2>
        <Campaign :shopCamData="shopCamData"></Campaign>
        <h2>オススメ商品</h2>
        <Movies :shopMovieList="shopMovieList"></Movies>
      </div>
    </div>
    <ElseSitesfrom :elseSiteList="elseSiteList"></ElseSitesfrom>
    <div class="shopHeader_2">
      <h2>d払いの使い方</h2>
      <Methods></Methods>
      <h2>よくあるご質問</h2>
      <Accordion></Accordion>
    </div>
    <BreadWarp></BreadWarp>
    <Footer></Footer>
  </div>
   <div class="only_m" v-else style="padding:20px">
    このページはモバイルバージョンでは表示できません。
  </div>
</template>

<script>
import Event from './components/Event.vue'
import NavBar from './components/NavBar.vue'
import Campaign from './components/Campaign.vue'
import Movies from './components/Movies.vue'
import ElseSitesfrom from './components/ElseSites.vue'
import shopCamData from './assets/shopCamData'
import shopMovieList from './assets/shopMovieList'
import elseSiteList from './assets/elseSiteList'
import Methods from './components/Methods.vue'
import Accordion from './components/Accordion.vue'
import BreadWarp from './components/BreadWarp.vue'
import Footer from './components/Footer.vue'

export default {
  name:'app',
  data(){
    return{
      view:1,
      shopCamData:shopCamData,
      shopMovieList:shopMovieList,
      elseSiteList:elseSiteList
    }
  },
  methods:{
    shuffle(array) {
    array.sort(() => Math.random() - 0.5);
      this.elseSiteList = array.slice(0,4)
    },
    handleResize(){
      this.width=window.innerWidth;
      this.height=window.innerHeight;
      if(this.width >= 479){
         this.view = 1
      }else{
         this.view = 0
      }
    },
    handleLoad(){
      this.width=window.innerWidth;
      this.height=window.innerHeight;
      if(this.width >= 480){
        this.view = 1
      }else{
        this.view = 0
        var app = document.querySelector('.only_m')
        var appOrigin = app.parentNode;
        appOrigin.style.minWidth="360px"
      }
    }
  },
  components:{
    Event,
    NavBar,
    Campaign,
    Movies,
    ElseSitesfrom,
    Methods,
    Accordion,
    BreadWarp,
    Footer,
  },
  mounted(){
    let newElseSiteList = [...this.elseSiteList]
    this.shuffle(newElseSiteList)
    window.addEventListener('resize',this.handleResize)
    window.addEventListener('load',this.handleLoad)
  }

  
  }



</script>

